import React from 'react'
import ReactDOM from 'react-dom'
import createHistory from 'history/createBrowserHistory'
import { BrowserRouter as Router, Route } from 'react-router-dom'

import 'assets/scss/material-kit-react.scss?v=1.4.0'

// pages for this product
//import Components from "views/Components/Components.jsx";

import LandingPage from 'views/LandingPage/LandingPage.jsx'
import ProfilePage from 'views/ProfilePage/ProfilePage.jsx'
import HonorPage from 'views/HonorPage/HonorPage.jsx'
import ResearchPage from 'views/ResearchPage/ResearchPage'
import LinksPage from 'views/LinksPage/LinksPage.jsx'
import ProjectsPage from './views/ProjectsPage/ProjectsPage'

/*import LoginPage from "views/LoginPage/LoginPage.jsx";*/

ReactDOM.render(
  <Router>
    <div>
      <Route exact path="/" component={LandingPage} />
      <Route exact path="/people" component={ProfilePage} />
      <Route exact path="/honor" component={HonorPage} />
      <Route exact path="/research" component={ResearchPage} />
      <Route exact path="/links" component={LinksPage} />
      <Route exact path="/projects" component={ProjectsPage} />
    </div>
  </Router>,
  document.getElementById("root")
)
