import React from "react";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";

// @material-ui/icons
import Backup from "@material-ui/icons/Backup";
import Build from "@material-ui/icons/Build";
import Info from "@material-ui/icons/InfoOutlined"
import Bookmarks from "@material-ui/icons/BookmarkBorder"
import SwapHoriz from "@material-ui/icons/SwapHoriz"

// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import NavPills from "components/NavPills/NavPills.jsx";
import pillsStyle from "assets/jss/material-kit-react/views/componentsSections/pillsStyle.jsx";

import Link from "@material-ui/core/Link"
import dbLinks from "views/LinksPage/DatabaseLinks"
import dataScienceLinks from "views/LinksPage/DataScienceLinks"
import xmlLinks from "views/LinksPage/xmlLinks"
import dbLabLinks from "views/LinksPage/DbLabLinks"

class SectionPills extends React.Component {
  render() {
    const { classes } = this.props;
    const dbLinksComponents = dbLinks.map(dbLink => {
      return (
        <p key={dbLink.id} className={classes.textLeft}>
          <Info
            style={{
              verticalAlign: "middle",
              marginRight: 8
            }}
          />
          <Link href={dbLink.url} target="_blank" rel="noreferrer noopener">
            {dbLink.name}
          </Link>
        </p>
      )
    })
    const dataScienceLinksComponents = dataScienceLinks.map(dataScienceLink => {
      return (
        <p key={dataScienceLink.id} className={classes.textLeft}>
          <Info
            style={{
              verticalAlign: "middle",
              marginRight: 8
            }}
          />
          <Link href={dataScienceLink.url} target="_blank" rel="noreferrer noopener">
            {dataScienceLink.name}
          </Link>
        </p>
      )
    })
    const xmlLinksComponents = xmlLinks.map(xmlLink => {
      return (
        <p key={xmlLink.id} className={classes.textLeft}>
          <Info
            style={{
              verticalAlign: "middle",
              marginRight: 8
            }}
          />
          <Link href={xmlLink.url} target="_blank" rel="noreferrer noopener">
            {xmlLink.name}
          </Link>
        </p>
      )
    })
    const dbLabLinksComponents = dbLabLinks.map(dbLabLink => {
      return (
        <p key={dbLabLink.id} className={classes.textLeft}>
          <Info
            style={{
              verticalAlign: "middle",
              marginRight: 8
            }}
          />
          <Link href={dbLabLink.url} target="_blank" rel="noreferrer noopener">
            {dbLabLink.name}
          </Link>
        </p>
      )
    })
    return (
      <div className={classes.section}>
        <div className={classes.container}>
          <div id="navigation-pills">
            <div className={classes.title}>
              <h3>Links</h3>
            </div>
            <GridContainer>
              <GridItem xs={12} sm={12} md={8} lg={6}>
                <NavPills
                  color="primary"
                  tabs={[
                    {
                      tabButton: "資料庫相關",
                      tabIcon: Backup,
                      tabContent: <div>{dbLinksComponents}</div>
                    },
                    {
                      tabButton: "資訊科學",
                      tabIcon: Bookmarks,
                      tabContent: <div>{dataScienceLinksComponents}</div>
                    },
                    {
                      tabButton: "XML相關",
                      tabIcon: Build,
                      tabContent: <div>{xmlLinksComponents}</div>
                    }
                  ]}
                />
              </GridItem>
              <GridItem xs={12} sm={12} md={12} lg={6}>
                <NavPills
                  color="rose"
                  horizontal={{
                    tabsGrid: { xs: 12, sm: 4, md: 4 },
                    contentGrid: { xs: 12, sm: 8, md: 8 }
                  }}
                  tabs={[
                    {
                      tabButton: "它校資料庫相關實驗室",
                      tabIcon: SwapHoriz,
                      tabContent: <div>{dbLabLinksComponents}</div>
                    }
                  ]}
                />
              </GridItem>
            </GridContainer>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(pillsStyle)(SectionPills);
